.searchResults {
  position: absolute;
  width: 100%;
  background: white;
  border-radius: 10px;
  overflow: hidden;
  margin-top: 5px;
  list-style-type: none;
  box-shadow: 0 10px 20px 5px rgba(0, 0, 0, 0.3);
  z-index: 1; }

.result:not(:last-child) {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1); }

.link {
  font-weight: 300;
  display: flex;
  justify-content: flex-start;
  padding: 5px;
  color: #0a0a0a;
  text-decoration: none;
  transition: background 0.1s ease, color 0.1s ease; }
  .link img {
    max-width: 24px;
    height: 100%; }
    @media (min-width: 500px) {
      .link img {
        max-width: 36px; } }
  .link .title {
    align-self: center;
    margin-left: 10px; }
  .link .year {
    color: #01d277;
    font-size: 0.85rem;
    font-weight: 100; }
  .link:hover {
    background: #01d277;
    color: white; }
    .link:hover .year {
      color: black; }



