.searchBar {
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
  border-radius: 10px;
  box-shadow: 0 10px 20px 5px rgba(0, 0, 0, 0.3); }

.input {
  width: 100%;
  height: 100%;
  background: transparent;
  border: none;
  outline: none;
  padding-right: 15px;
  font-size: 1rem;
  transition: padding-left 0.2s ease; }
  .input:focus {
    padding-left: 10px; }

.icon {
  color: #141414;
  padding: 0 10px; }



