.card {
  display: flex;
  margin: 15px 4%;
  flex: 1 42%;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
  box-shadow: 0 10px 20px 5px rgba(0, 0, 0, 0.3);
  background: #333;
  transition: transform 0.25s ease-in-out; }
  .card:hover {
    transform: scale(1.075); }
    .card:hover .details {
      opacity: 1; }
  @media (min-width: 700px) {
    .card {
      margin: 15px 2%;
      flex: 1 21%; } }

.details {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  opacity: 0;
  position: absolute;
  color: white;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  transition: opacity 0.1s ease;
  background: linear-gradient(to top, rgba(10, 10, 10, 0.85), rgba(10, 10, 10, 0.5)); }

.title {
  font-size: 0.8rem;
  align-self: center;
  padding: 10px;
  margin-bottom: 5px;
  text-align: center; }

.voteAverage {
  align-self: center;
  font-size: 2rem;
  color: #01d277; }



