header {
  padding-bottom: 20px; }
  @media (min-width: 700px) {
    header {
      padding-bottom: 0; } }

.container {
  display: flex;
  flex-direction: column; }
  @media (min-width: 700px) {
    .container {
      flex-direction: row;
      justify-content: space-between; } }

.logo {
  align-self: center;
  padding: 30px 0; }
  @media (min-width: 700px) {
    .logo {
      padding-left: 20px;
      flex: 1; } }



